import React from "react"
import { useStaticQuery, graphql } from "gatsby";
import Layout from "../components/layout/Layout";
import { Grid } from "@material-ui/core"
import { ResumeData } from "../data/ResumeData";
import { BgImage, convertToBgImage } from "gbimage-bridge"
import BackgroundImage from 'gatsby-background-image'
import { getImage } from "gatsby-plugin-image"
import { StaticImage } from "gatsby-plugin-image"
import "../styles/AppStyle.css"
import GatsbyImage from "gatsby-image";

export default function About() {

	const { backgroundImage } = useStaticQuery(
		graphql`
		  query {
			backgroundImage: file(relativePath: { eq: "mt_elbert.jpg" }) {
			  childImageSharp {
				gatsbyImageData(
				  height: 800
				  quality: 100
				  placeholder: BLURRED
				  formats: [AUTO, WEBP, AVIF]
				)
			  }
			}
		  }
		`
	)

	console.log(backgroundImage);
	const image = getImage(backgroundImage)
	console.log(image);
	// Use like this:
	const bgImage = convertToBgImage(image)

	return(
		<Layout>
			<BgImage
				image={image}
				// Spread bgImage into BackgroundImage:
				// {...bgImage}
				// preserveStackingContext
				backgroundColor={`#040e18`}
				//@ts-ignore
				style={{
					backgroundPosition: 'top center',
					backgroundRepeat: 'none',
					backgroundSize: 'cover',
					minHeight: '430px',
					height: '70vh',
					marginBottom: 60,
					position: 'relative'
				}}
			>
				<div style={{ fontWeight: 550, fontSize: '16pt', padding: 20, color: 'white', backgroundColor: 'rgba(0,0,0,0.3)', position: 'absolute', top: '20%', left: '3%', borderRadius: 15}}>
					Hi.  My name is Bryce.
				</div>
			</BgImage>
			
			<Grid container spacing={2}>
				<Grid item md={4} xs={12}>
					<div style={{ marginBottom: `1.45rem` }}>
						<StaticImage src="../images/tacos.jpg" alt="Feed Me Tacos" />
					</div>
				</Grid>
				<Grid item md={8} xs={12}>
					<div className="box" style={{padding: 20, color: 'white'}}>
						<div>
							I asked a former software engineer how someone with the skills suited for Silicon Valley could help with crucial
							problems like climate change, conservation, and sustainability.  His answer? <i>"Quit the industry and start a farm"</i>, which is exactly what he had done years before.
						</div>
						<div style={{marginTop: 20}}>
							I obviously did not take that advice, despite becoming something of an avid horticulturalist now.
						</div>
						<div style={{marginTop: 20}}>
							I studied aerospace engineering at Purdue Univerity and moved into web and software development.  Learning to build rockets
							and web applications is awesome, but now I look to take these skills and find a bridge between using them in the digital frontier 
							and the natural world.
						</div>
						<div style={{marginTop: 20}}>
							This site is something of a sandbox and a stream of consciousness of my thoughts and ideas.  Half the time it doesn't work, the other half it doesn't make any sense.  I regularly break it.
							But it contains all my ideas into a single organized space, as well as keeping me on task.  A place to just toss out some concepts, document their successes and/or failures, and provide
							a modicum of entertainment for the occasional visitor.
						</div>
					</div>
				</Grid>
			</Grid>
			<div style={{marginTop: 40}}>

			
				<Grid container spacing={2}>
					
					<Grid item md={4} xs={12}>
						<div style={{ height: '100%', marginBottom: `1.45rem`, marginTop: 40 }}>
							<StaticImage src="../images/havasu.jpg" alt="Havasu Falls" />
						</div>
					</Grid>
					<Grid item md={8} xs={12}>
						<div id="resume">
							<div style={{fontSize: '16pt', fontWeight: 500, marginTop: 10, marginBottom: 10, color: 'white'}}>Work</div>

							{
								ResumeData.map(item => (
									<div key={item.id} className="resume-item box" style={{padding: 20, color: 'white'}} >
										<h4>{item.role}</h4>
										<h5>{item.where}</h5>
										<ul>
											{
												item.details.map((detail, index) => (
													<li key={detail + index}>{detail}</li>
												))
											}
											</ul>
									</div>
								))
							}
						</div>
					</Grid>
				</Grid>
			</div>
		</Layout>

	)
}
